
.footer-top__ssi svg{
	cursor: pointer;
}
.footer-top__ssi svg rect{
	stroke: #fff;
}
.footer-top__ssi svg path{
	fill: #fff;
}
.footer-top__ssi:hover svg rect{
	stroke: #801f25;
	fill: #fff;
}
.footer-top__ssi:hover svg path{
	fill: #801f25;
}



