
.modal_box_close{
  background: url(../assets/img/close.svg);
  width: 30px;
  height: 30px;
  background-size: cover;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}
#find_moto{
  position: absolute;
  width: 760px;
  padding: 20px; 
  z-index: 100;
  background: #fff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.3);
  top: 69px;
  display: none;
  border: 1px solid #eee;
}
#find_moto:after{
  content: '';
  border: 9px solid transparent;
  border-bottom: 9px solid #fff;
  position: absolute;
  top: -18px;
  left: 45px;
}
#find_moto:before{
  content: '';
  border-top: 9px solid transparent;
  position: absolute;
  display: block;
  width: 100%;
  top: -10px;
  left: 0;
}
#show-modal-findmoto:hover #find_moto{
  display: block;
}
.find-fade-enter-active {
  transition: all .3s ease;
}
.find-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.find-fade-enter, .find-fade-leave-to{
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
#find_moto h2{
  text-align: center;
  font-size: 34px;
}
.years{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.years div{
  padding: 3px;
  margin: 3px;
  background: #e7e7e7;
}
.years>div:hover, .years>div.current{
  background: #801f25;
  color: #fff;
}
.form-group{
  position: relative;
  margin-bottom: 15px;
}
.title-group{
  padding-bottom: 15px;
}
.slider-mark img{
  max-height: 100%;
  max-width: 100%;
}
.form-group .slider-marks{
  margin-right: -40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: relative;
  width: 100%;
}
.form-group .slider-mark{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 16.66%;
  margin-bottom: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 10px;
  border: 1px solid #e7e7e7;
}
.slider-mark:hover, .slider-mark.current{
  border: 1px solid #801f25;
  cursor: pointer;
}
.prev-next{
  position: absolute;
  right: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.prev-result, .next-result{
  background: #801f25;
  width: 22px;
  height: 22px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #fff;
}
.prev-result:not(.disabled):hover, .next-result:not(.disabled):hover{
  box-shadow: 0 0px 6px rgba(103, 70, 70, 0.7);
  cursor: pointer;
}
.prev-result.disabled, .next-result.disabled{
  background: #fff;
  color: #000;
  border: 1px solid #000;
}
.prev-result{
  margin-right: 5px;
}
.next-result{
}
.result-count{
  font-weight: 600;
  font-size: 30px;
  padding: 10px 0
}
#find_moto .search-detail{
  width: 220px;
  height: 50px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-decoration: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
@media screen and (max-width: 375px) {
#find_moto{
    width: 300px;
    padding: 10px;
}
#find_moto h2 {
    font-size: 24px;
    margin: 10px 0;
}
.result-count {
    font-size: 18px;
}
#find_moto .search-detail{
    font-size: 12px;
    padding: 10px;
    height: auto;
    width: 100%;
}
.form-group .slider-mark{
    margin-bottom: 5px;
    font-size: 11px;
    padding: 5px;
    width: 33.33%;
}
.title-group {
    padding-bottom: 10px;
}
.form-group {
    margin-bottom: 10px;
}
}



