
.my-image-sliders{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.93);
  z-index: 100;
}
.my-image-sliders__close{
  width: 20px;
  height: 20px;
  position: absolute;
  right: 4%;
  top: 5%;
  cursor: pointer;
  z-index: 1;
}
.image-slider{
  position:relative;    
  width: 100%;
  height: 100%;
  display: block;
}
.image-slider img{
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  max-width: 90%;
  max-height: 90%;
}
.my-image-sliders-prev{
  position: absolute;
  left: 4%;
  top: 50%;
  -webkit-transform: translate(0,-50%);
          transform: translate(0,-50%);
  cursor: pointer;
}
.my-image-sliders-next{
  position: absolute;
  right: 4%;
  top: 50%;
  -webkit-transform: translate(0,-50%);
          transform: translate(0,-50%);
  cursor: pointer;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 3s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}



